






.pokemon-container {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
min-block-size: calc(4,5);
}

.all-container {
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
min-width: 0;
min-height: 0;
max-width: 4;
max-height: 5;
}

.thumb-container {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
padding: 1.5rem 0.2rem;
margin: 0.3rem;
border: 1px solid #efefef;
border-radius: 2rem;
min-width: 160px;
text-align: center;
box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
}



h3 {
margin-bottom: 0.2rem;
}



.thumb-container img {
width: 120px;
height: 120px;
}

.thumb-container small {
text-transform: capitalize;
}

.detail-wrapper {
display: flex;
flex-direction: column;
width: 100%;
margin:50px
}

.detail-wrapper button {
color: rgb(22, 22, 22);
padding: 0.5rem;
margin-top: 1rem;
border: none;
border-radius: 0.2rem;
cursor: pointer;
background-color: rgba(0, 0, 0, 0.185);
}

.load-more {
border-radius: 6px;
border: 1px solid #c6c6c6;
color: #444;
padding: 0.5rem 1.5rem;
min-width: 20%;
margin-top: 1rem;
font-family: "Satisfy", cursive;
}



.pokeinfo {
margin: 0 25px;
font-family: "Satisfy", cursive;

}





